<template lang="md">
  # Table

  ---

  

  ## 

  - 
  - 

  ## 

  ```html
  data () {
    return {
      colums: ['id', 'Name', 'Age', 'Address'],
      datas: [],
      itemActions: [{
        name: 'edit',
        title: ''
      }, {
        name: 'copy',
        title: ''
      }, {
        name: 'delete',
        title: ''
      }]
    }
  },
  created () {
    var self = this
    const mockData = []
    for (let i = 0; i < 20; i++) {
      mockData.push({
        id: i,
        name: `Edward King ${i + 1}`,
        age: `${i + 20}`,
        address: `London, Park Lane no. ${i + 1}`,
        phone: `13535135${i > 10 ? i : '0' + i}`
      })
    }
    self.datas = mockData
  }
  ```

  

  ```html
  events: {
    'table:action' (action, data) {
      console.log('table:action', action, data)
    }
  }
  ```

  ## API

  |  |  |  |  |
  |------|-----|------|-------|
  | caption | 	| String | undefined |
  | colums |  | Array | [] |
  | dataSource |  | Array | [] |
  | itemActions | `$event``table:action`actiondata | Array | [] |
  | striped |  | Boolean | false |
  | bordered |  | Boolean | false |
  | hover |  | Boolean | true |
  | condensed |  | Boolean | false |
  | rowSelection |  | Boolean | false |
  | selectedRowKeys | ```````` | Boolean | false |
  | showItem |  | Boolean | false |
  | current |  | Number | 1 |
  | limit |  | Number | 10 |
  | locale |  | {} | {} |
  | showReload | `$event``table:reload` | Boolean | false |

  ## 

  <demo>
    <example title="">
      <v-table caption="Optional table caption." :colums="colums" :limit="4" :data-source="datas" :item-actions="itemActions" show-reload>
      </v-table>
    </example>
    <example title="">
      <v-table :colums="colums" :data-source="datas" striped :limit="4" :item-actions="itemActions">
      </v-table>
    </example>
    <example title="">
      <v-table :colums="colums" :data-source="datas" bordered :limit="4" :item-actions="itemActions">
      </v-table>
    </example>
    <example title="">
      <v-table :colums="colums" :data-source="datas" condensed :limit="4" :item-actions="itemActions">
      </v-table>
    </example>
    <example title="">
      <v-table :colums="colums" :data-source="datas1" :item-actions="itemActions">
      </v-table>
    </example>
    <example title="">
      <v-table :colums="colums" :data-source="datas" :item-actions="itemActions" row-selection>
      </v-table>
    </example>
    <example title="">
      <v-table :colums="colums" :data-source="datas" :target-selected-keys="targets" row-selection>
        <div slot="table-operation">
          <v-button type="primary" :loading="loading" @click="_reload"></v-button>
        </div>
      </v-table>
    </example>
    <example title="">
      <v-table :colums="colums" :data-source="datas" :item-actions="itemActions" row-selection selected-row-keys show-item>
      </v-table>
    </example>
  </demo>
</template>
<script>
import vTable from '../../components/table/Table'
import vButton from '../../components/button'
export default {
  data() {
    return {
      colums: ['Id', 'Name', 'Age', 'Address'],
      datas: [],
      datas1: [],
      itemActions: [{
        name: 'edit',
        title: ''
      }, {
        name: 'copy',
        title: ''
      }, {
        name: 'delete',
        title: ''
      }],
      loading: false,
      targets: []
    }
  },
  components: {
    vTable,
    vButton
  },
  events: {
    'table:action' (action, data) {
      console.log('table:action', action, data)
      if (action === 'delete') {
        // this.datas.splice(data.id, 1)
      }
    },
    'table:reload' () {
      console.log('table:reload')
    }
  },
  created() {
    var self = this
    const mockData = []
    for (let i = 0; i < 50; i++) {
      mockData.push({
        id: i * 2,
        name: `${i % 3 > 1 ? 'Lao King' : 'Lao Wang'} ${i}`,
        age: `${i + 20}`,
        address: `${i % 3 > 1 ? 'London' : 'BeiJing'}, Park Lane no. ${i}`,
        phone: `13535135${i > 10 ? i : '0' + i}`
      })
    }
    self.datas = mockData
    mockData.filter(item => +item.key % 3 > 1).push(item => item.rowClassName)
    self.datas1 = mockData
  },
  methods: {
    _reload() {
      this.loading = true
      setTimeout(() => {
        this.targets = []
        this.loading = false
      }, 1000)
    }
  }
}
</script>



