<template lang="md">
  # Upload

  ---

  

  ## 

  

  - 
  - 
  - 

  ## API

  |  |  |  |  |
  |------------|--------------------------------------------------------------| ----------- |-------|
  | operation | ,  | String | '' |
  | headers |  | Object | {} |
  | multiple |  | Boolean | false |
  | data |  | Object | {} |
  | name |  | String | `file` |
  | withCredentials | `cookie` | Boolean | false |
  | showUploadList |  | Boolean | true |
  | type | `select``drag` | String | `select` |
  | accept |  | String | undefined |
  | format |  accept format accept  input  accept  | Array | [] |
  | maxSize | `kb` | Number | 2048 |
  | beforeUpload |  false  Promise  | function |  |
  | onProgress |  `event`, `file`, `fileList` | function |  |
  | onSuccess |  `response`, `file`, `fileList` | function |  |
  | onError |  `error`, `file`, `fileList` | function |  |
  | onPreview | `file`  file.response  | function |  |
  | onRemove |  `file`, `fileList` | function |  |
  | onFormatError |  `file`, `fileList` | function |  |
  | onExceededSize |  `file`, `fileList` | function |  |
  | defaultFileList |  | Array | [] |

  ### defaultFileList

  ```js
  [{
    name: 'image1.jpg',
    url: 'http://www.xxx.com/image1.jpg'
  }, {
    name: 'image2.jpg',
    url: 'http://www.xxx.com/image2.jpg'
  }]
  ```

  ## 

  <demo>
    <example title="">
      <v-upload :on-exceeded-size="onExceededSize">
        <v-button type="default"><v-icon type="upload"></v-icon></v-button>
      </v-upload>
    </example>
    <example title="">
      <v-upload :on-exceeded-size="onExceededSize" multiple>
        <v-button type="default"><v-icon type="upload"></v-icon></v-button>
      </v-upload>
    </example>
    <example title="">
      <v-upload :on-exceeded-size="onExceededSize" multiple type="drag">
        <div class="drag">
          <v-icon type="upload"></v-icon>
          <p></p>
        </div>
      </v-upload>
    </example>
  </demo>
</template>
<style scoped lang="less">
  .drag {
    padding: 20px 0;

    .icon {
      font-size: 24px;
      color: #21A3E9;
    }
  }
</style>
<script>
  import vUpload from '../../components/upload'
  import vButton from '../../components/button'
  import vIcon from '../../components/iconfont'
  export default {
    components: {
      vUpload,
      vButton,
      vIcon
    },
    methods: {
      onExceededSize (file, fileList) {
        console.log(file.name, fileList)
      }
    }
  }
</script>



